import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useRegisterMutation } from 'services/authApi';
import { setAuthToken } from 'redux/auth/slice';
import { Button } from '@chakra-ui/react';
import Notiflix from 'notiflix';

const saveAuthToken = (token) => {
  localStorage.setItem('token', token);
};

const RegisterForm = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [register] = useRegisterMutation();
  const dispatch = useDispatch();

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      const userData = await register({ name, email, password }).unwrap();
      saveAuthToken(userData.token);
      dispatch(setAuthToken(userData.token));
      Notiflix.Notify.success('Registration success!');
    } catch (error) {
      Notiflix.Notify.failure(error?.data?.message || 'Registration failed. Please try again.');
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <label>
        Name:
        <input
          type="text"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
          pattern="^[A-Za-z\s]+$"
          title="Name should only contain letters and spaces."
        />
      </label>
      <label>
        Email:
        <input
          type="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
          pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
          title="Please enter a valid email address."
        />
      </label>
      <label>
        Password:
        <input
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
          pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
          title="Password must be at least 8 characters long and include at least one uppercase letter, one lowercase letter, and one digit."
        />
      </label>
      <Button type="submit">Register</Button>
    </form>
  );
};

export default RegisterForm;

